@font-face {
  font-family: 'AvenirNextLTPro';
  font-style: normal;
  font-weight: normal;
  src: local('AvenirNextLTPro'), url('./components/AvenirNextLTPro-Regular.woff') format('woff');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'AvenirNextLTPro', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  color: #292d35;
  text-decoration: none;
}

a:hover{
  color: #282c34;
  text-decoration: none;
}