.mealCard{
    background-color: white;
    border: 1px black dashed;
    padding: 1em;
    margin: 1em;
}

.dayCard{
    background-color: silver;
    padding: 1em;
    margin: 1em 0px 1em 1em 0px;
    border-width: 1px 0px 0px 0px;
    border-color: black;
    border-style: dashed;
}

.mealImg{
    border: 1px black solid;
    display: block;
    margin: 0 auto;
    max-width: 100%;
    height: auto;
}

ul{
    list-style-type: none;
}